@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}body{background-image:linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%)}

.tabs{height:100vh;max-width:500px;margin:0 auto;display:flex;flex-direction:column;justify-content:center;align-items:center}.tabs__btns{max-width:100%;width:100%;display:flex;justify-content:space-between;align-items:center}.tabs__btns-btn{font-family:'Lato', sans-serif;font-size:16px;font-weight:700;letter-spacing:2px;text-transform:uppercase;max-width:100%;width:100%;border:2px solid #fff;background-color:#fff;padding:10px 0;outline:none;cursor:pointer;transition:border 0.5s ease}.tabs__btns-btn.is-active{border:2px solid pink;color:pink}.tabs__btns-btn:hover{border:2px solid pink}.tabs__content{background-color:#fff;padding:10px;border-bottom:2px solid pink;border-left:2px solid pink;border-right:2px solid pink}.tabs__item{font-size:16px;font-family:'Lato', sans-serif;font-weight:400;display:none}.tabs__item.is-active{display:block}

